import React from "react"
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import Home from "./components/pages/Home.js"
import Archiv from "./components/pages/Archiv.js"
import Results from "./components/pages/Results"
import Drivers from "./components/pages/Drivers.js"
import Teams from "./components/pages/Teams.js"
import Calendar from "./components/pages/Calendar.js"


import MyNavbar from "./components/MyNavbar.js";
import Header from "./components/Header.js";
import Ads from "./components/Ads.js";
import Footer from "./components/Footer.js";
import {Navbar, Nav} from 'react-bootstrap';
import { Redirect } from 'react-router-dom';

class App extends React.Component{
    render() {
        return (
            <BrowserRouter>
                <Ads />
                <Header />
                
                <Redirect exact from="" to="/f1online-dev/index" />
                <Redirect exact from="/" to="/f1online-dev/index" />
                <Redirect exact from="/f1online-dev" to="/f1online-dev/index" />
                <Redirect exact from="/f1online-dev/" to="/f1online-dev/index" />
                <Switch>
                    <Route path="/f1online-dev/index" component={Home} exact/>
                    <Route path="/f1online-dev/clanky" component={Archiv} exact/>
                    <Route path="/f1online-dev/vysledky" component={Results} exact/>
                    <Route path="/f1online-dev/piloti" component={Drivers} exact/>
                    <Route path="/f1online-dev/timy" component={Teams} exact/>
                    <Route path="/f1online-dev/kalendar" component={Calendar} exact/>
                </Switch>
                <Footer />
            </BrowserRouter>    
        )
    }
}

export default App